.Floor1 {
  display: block;
  width: 75%;
  max-width: 360px;
  margin: 20px auto;
}

.main-bottom-block {
    display: inline-block;
    font-size: 10px;
    padding: 20px;
    padding-top: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    width: 92px;
    height: 92px;
}
.main-user-photo {
    height: 20%;
    width: 20%;
    max-height: 90px;
    max-width: 90px;
    display: block;
    margin-top: -10%;
    border: 4px solid #fff;
    -webkit-box-shadow: 0 4px 40px #d9d9fb;
    box-shadow: 0 4px 40px #d9d9fb;
    border-radius: 50%;
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
    border-bottom-right-radius: 50%;
    border-bottom-left-radius: 50%;
}

.main-text-info-block-today {
    font-family: GothamPro;
    font-size: 15px;
    line-height: normal;
    text-align: center;
    color: #fff;
    font-weight: inherit;
}
.limit-block {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
    justify-content: center;
    margin-top: 16px;
}
.main-bottom-block {
    display: inline-block;
    font-size: 10px;
    padding: 20px;
    width: 92px;
    height: 92px;
}
.main-info-block-today {
    margin-left: calc(50% - 142px);
    border-radius: 24px;
    float: left;
    background: url(/main_blue_block.svg) 50%;
    background-image: url(/main_blue_block.svg);
    background-position-x: 50%;
    background-position-y: center;
    background-size: initial;
    background-repeat-x: initial;
    background-repeat-y: initial;
    background-attachment: initial;
    background-origin: initial;
    background-clip: initial;
    background-color: initial;
}